body {
  text-align: center;
  background-color: #f6f6f6; }

input {
  border-style: groove; }

button {
  border-style: groove; }

.shadow {
  box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.03); }

.chat-mag-wrap {
  width: 100%;
  display: flex;
  flex-flow: row wrap; }
  .chat-mag-wrap .chat-msg {
    margin: 1rem 1rem;
    padding: 0.1rem 1rem;
    max-width: 25rem;
    min-height: 3rem;
    border-radius: 1.5rem;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center; }
  .chat-mag-wrap .chat-right {
    justify-content: flex-end; }
    .chat-mag-wrap .chat-right div {
      color: white;
      background: #845ef7; }
  .chat-mag-wrap .chat-left {
    justify-content: flex-start; }
    .chat-mag-wrap .chat-left div {
      color: #495057;
      background: #dbe4ff; }
